<template>
	<div class="row">
		<app-stock v-for="stock in stocks" :stock="stock"></app-stock>
	</div>
</template>

<script type="text/javascript">
	import Stock from './Stock.vue'

	export default {
		components: {
			appStock: Stock,
		},
		computed: {
			stocks() {
				return this.$store.getters.stocks;
			}
		}
	}
</script>


