<template>
	<div class="row">
		<div class="col-md-12">
			<h1>Trade or View your Portofolio</h1>
			<h6>You may Save & Load your Data</h6>
			<h6>Click on 'End Day' to begin a New Day!</h6>
			<hr>
			<p>Your Funds: {{ funds | currency}}</p>
		</div>
	</div>
</template>

<script type="text/javascript">
	export default {
		computed: {
			funds() {
				return this.$store.getters.funds;
			}
		}
	}
</script>


